// src/pages/JobBoard.js  src/components/JobBoard.js
import React, { useState } from 'react';
import JobDetail from '../subpages/JobDetail';

const JobBoard = () => {
  const [selectedJob, setSelectedJob] = useState(null); 
  const jobs = [
    { id: 1, title: '', company: 'XYZ ', location: '', type: '' },
    { id: 2, title: '', company: 'ABC ', location: '', type: '' },
    { id: 3, title: '', company: '123 ', location: '', type: '' },
    { id: 4, title: '', company: 'DEF ', location: '', type: '' },
    { id: 5, title: 'UI/UX ', company: 'GHI ', location: '', type: '' },
    { id: 6, title: '', company: 'JKL ', location: '', type: '' },
    { id: 7, title: '', company: 'MNO ', location: '', type: '' },
    { id: 8, title: '', company: 'PQR ', location: '', type: '' },
    { id: 9, title: 'HR ', company: 'STU ', location: '', type: '' },
    { id: 10, title: '', company: 'VWX ', location: '', type: '' },
    { id: 11, title: 'iOS ', company: 'YZ ', location: '', type: '' },
    { id: 12, title: '', company: 'ABC ', location: '', type: '' }
  ];

  const firstJob = jobs.length > 0 ? jobs[0] : null; 

  const handleJobClick = (job) => {
    setSelectedJob(job);
  };

  const handleBackClick = () => {
    setSelectedJob(null);
  };

  
   return (
    <div className="mt-16 lg:mr-64 container mx-auto px-4 py-16 lg:px-64">
      <h1 className="text-4xl font-bold text-center mb-8 text-gray-900"></h1>
      {selectedJob ? (
        <JobDetail job={selectedJob} onBackClick={handleBackClick} />
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
          {jobs.map((job) => (
            <div key={job.id} className="bg-white rounded-lg border border-gray-300 shadow hover:shadow-md transition-shadow duration-300 p-6 cursor-pointer" onClick={() => handleJobClick(job)}>
              <h2 className="text-xl font-semibold text-gray-800 mb-3">{job.title}</h2>
              <p className="text-gray-700 font-medium">{job.company}</p>
              <p className="text-gray-600">{job.location}</p>
              <span className="inline-block bg-green-200 text-green-800 px-3 py-1 rounded-full text-sm font-medium mt-4">{job.type}</span>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default JobBoard;
